
.card img {
    margin-left: -30px;
    height: 250px;
}

.card__thumbnail {
    position: relative;
}

.card__thumbnail:hover {
    cursor: pointer;
}

.card_hide {
    opacity: 0;
    transition: 1s ease;
}

.card_show {
    transition: 1s ease;
    opacity: 1;
}

.card__discount-price {
    color: #f33535;

}

.card__old-price {
    text-decoration: line-through;
    color: #9b9b9b;
    font-size: .9em !important;
}


.favorite {
    background-color: #f33535 !important;
    color: white !important;
}